import { pageUrl } from "../env";

const pageRequest = {
    home: {
        isHome: true,
        isMenuRight: true,
        // GHP Build
        isPwaPopup: false,
        // PROD & PREVIEW Build
        // isPwaPopup: true,
    },
    login: {
        isLogIn: true,
    },
    registration: {
        isRegistration: true,
    },
    editprofile: {
        isEditProfile: true,
    },
    forgotpassword: {
        isForgotPassword: true,
        isMenuRight: true,
    },
    changepassword: {
        isChangePassword: true,
        isMenuRight: true,
    },
    legalacceptances: {
        isLegalacceptances: true,
        isMenuRight: true,
    },
    lung: {
        isCt1: true,
        isMenuRight: true,
        isPwaPopup: false,
    },
    L1CT: {
        isCt2: true,
        isMenuRight: false,
        isPwaPopup: false,
    },
    L1CTSD: {
        isMenuRight: true,
        isCt3: true,
        isPwaPopup: false,
    }
}

export default () => {
    const textUrl = window.location.host + window.location.pathname;

    if (textUrl === pageUrl.home || textUrl === pageUrl.homeIndex) {
        return pageRequest.home;
    }
    if (textUrl === pageUrl.login) {
        return pageRequest.login;
    }
    if (textUrl === pageUrl.registration) {
        return pageRequest.registration;
    }
    if (textUrl === pageUrl.editprofile) {
        return pageRequest.editprofile;
    }
    if (textUrl === pageUrl.changepassword) {
        return pageRequest.changepassword;
    }
    if (textUrl === pageUrl.forgotpassword) {
        return pageRequest.forgotpassword;
    }
    if (textUrl === pageUrl.legalacceptances) {
        return pageRequest.legalacceptances;
    }
    if (textUrl === pageUrl.lung) {
        return pageRequest.lung;
    }
    if (textUrl === pageUrl.L1CT) {
        return pageRequest.L1CT;
    }
    if (textUrl === pageUrl.L1CTSD) {
        return pageRequest.L1CTSD;
    }
}